span',[_vm._v("3.1. Đôi khi chúng tôi có thể sử dụng \"cookie\" hoặc các tính năng khác để cho phép chúng tôi hoặc các bên thứ ba thu thập hoặc chia sẻ thông tin sẽ giúp chúng tôi cải thiện Nền tảng của mình và Các Dịch Vụ chúng tôi cung cấp, hoặc giúp chúng tôi đưa ra các dịch vụ và tính năng mới. \"Cookie\" là các mã danh định chúng tôi gửi đến máy tính hoặc thiết bị di động của bạn, cho phép chúng tôi nhận dạng máy tính hoặc thiết bị của bạn và cho chúng tôi biết khi nào Các Dịch Vụ hoặc Nền tảng được sử dụng hay truy cập, bởi bao nhiêu người và để theo dõi những hoạt động trong Các Nền tảng của chúng tôi. Chúng tôi có thể liên kết thông tin cookie với dữ liệu cá nhân. Cookie cũng liên kết với thông tin về những nội dung bạn đã chọn đối với các trang mua sắm bạn đã xem, hoặc các game bạn đã chơi. Thông tin này được sử dụng để ví dụ như theo dõi giỏ hàng. Cookie cũng được sử dụng để cung cấp nội dung dựa trên quan tâm của bạn và để theo dõi việc sử dụng của các Dịch Vụ.")]),_c('span',[_vm._v(" 3.2. Bạn có thể từ chối sử dụng cookie bằng cách chọn các thiết lập thích hợp trên trình duyệt của bạn. Tuy nhiên, vui lòng lưu ý rằng nếu bạn thực hiện thao tác này bạn có thể không sử dụng được các chức năng đầy đủ của Nền tảng hoặc Các Dịch Vụ của chúng tôi.")])])]),_c('div',{staticClass:"content-title"},[_c('h4',{staticClass:"content-heading"},[_vm._v(" 4. BẢO VỆ VÀ LƯU TRỮ THÔNG TIN KHÁCH HÀNG BẰNG CÁCH NÀO ")]),_c('p',{staticClass:"content-title__item"},[_vm._v(" Chúng tôi thực hiện các biện pháp bảo mật khác nhau và luôn nỗ lực để đảm bảo sự an toàn của dữ liệu cá nhân của bạn trên các hệ thống của chúng tôi. Dữ liệu cá nhân của người dùng được lưu trữ đằng sau các mạng bảo mật và chỉ có thể được truy cập bởi một số nhân viên có quyền truy cập đặc biệt đến các hệ thống của chúng tôi. ")])]),_c('div',{staticClass:"content-title"},[_c('h4',{staticClass:"content-heading"},[_vm._v(" 5. THẮC MẮC, QUAN NGẠI HOẶC KHIẾU NẠI? LIÊN HỆ VỚI CHÚNG TÔI ")]),_c('p',{staticClass:"con